* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.list {
  list-style: none;
}

.country-info__span{
  font-weight: bold;
  font-style: oblique;
  padding-right: 5px;
}

.country-info__item,
.country-list__item{
  display: flex;
  align-items: center;
}

.country-info__title,
.country-list__title{
  padding-left: 5px;
}